'use client'

import React, { useRef } from 'react'
import { Canvas, useFrame } from '@react-three/fiber'
import { Sphere, MeshDistortMaterial } from '@react-three/drei'
import * as THREE from 'three'

function AnimatedOrb() {
  const meshRef = useRef<THREE.Mesh>(null)
  const materialRef = useRef<THREE.MeshDistortMaterial>(null)

  useFrame((state) => {
    if (materialRef.current) {
      // Pulse effect for distortion
      const t = state.clock.getElapsedTime()
      materialRef.current.distort = 0.3 + Math.sin(t * 1.5) * 0.1
      materialRef.current.speed = 2 + Math.cos(t * 0.5) * 0.5
    }
    
    if (meshRef.current) {
      // Gentle floating rotation
      meshRef.current.rotation.x = Math.sin(state.clock.getElapsedTime() * 0.2) * 0.1
      meshRef.current.rotation.y += 0.005
    }
  })

  return (
    <Sphere args={[1, 64, 64]} ref={meshRef} scale={1}>
      <MeshDistortMaterial
        ref={materialRef}
        color="#B31B1B"
        emissive="#B31B1B"
        emissiveIntensity={0.5}
        roughness={0.2}
        metalness={0.1}
        distort={0.3}
        speed={2}
      />
    </Sphere>
  )
}

function GlowEffect() {
  return (
    <pointLight position={[0, 0, 0]} intensity={15} color="#B31B1B" distance={5} decay={2} />
  )
}

export default function GlowingOrb() {
  return (
    <div className="hidden lg:block fixed top-1/2 right-[-10vw] -translate-y-1/2 w-[50vw] h-[50vh] pointer-events-none z-[1] opacity-80 mix-blend-multiply">
      <Canvas camera={{ position: [0, 0, 4], fov: 45 }}>
        <ambientLight intensity={0.4} />
        <pointLight position={[10, 10, 10]} intensity={2} color="#ffffff" />
        <pointLight position={[-10, -10, -10]} intensity={0.5} color="#B31B1B" />
        <GlowEffect />
        <AnimatedOrb />
      </Canvas>
    </div>
  )
}
